/**
* ----------------------------------------------
* Demo styles
* ----------------------------------------------
**/
.u-position-absolute {
  position: absolute; }

.u-position-relative {
  position: relative; }

.accordion__item--has-icon {
  position: relative; }

.accordion__title {
  color: #000;
  cursor: pointer;
  width: 100%;
  text-align: left;
  border: none; }

.accordion__title:hover,
.accordion__title:focus {
  color: #1C5ED3;
  outline: none; }

.accordion__body {
  padding: 8px 20px;
  display: block;
  animation: move-down 0.5s ease-in;
  border-left: 5px solid #E54E0C;
  margin: 8px 0; }
  .accordion__body p {
    margin: 0 0 7px; }
  .accordion__body a {
    font-weight: 700; }

.accordion__body--hidden {
  display: none;
  opacity: 0;
  animation: move-up 0.5s ease-in; }

.accordion__title > *:last-child,
.accordion__body > *:last-child {
  margin-bottom: 0; }

.accordion__arrow {
  display: inline-block;
  position: relative;
  width: 24px;
  height: 12px;
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -6px; }

.accordion__arrow::after,
.accordion__arrow::before {
  display: block;
  position: absolute;
  top: 50%;
  width: 10px;
  height: 2px;
  background-color: currentColor;
  content: ''; }

.accordion__arrow::before {
  left: 4px;
  transform: rotate(45deg); }

[aria-expanded='true'] .accordion__arrow::before, [aria-selected='true'] .accordion__arrow::before {
  transform: rotate(-45deg); }

.accordion__arrow::after {
  right: 4px;
  transform: rotate(-45deg); }

[aria-expanded='true'] .accordion__arrow::after, [aria-selected='true'] .accordion__arrow::after {
  transform: rotate(45deg); }

.accordion__arrow::before,
.accordion__arrow::after {
  transition: transform 0.25s ease; }

/* -------------------------------------------------- */
/* ---------------- Animation part ------------------ */
/* -------------------------------------------------- */
@keyframes fadein {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes move-down {
  0% {
    transform: translateY(0); }
  10% {
    transform: translateY(0); }
  20% {
    transform: translateY(5px); }
  30% {
    transform: translateY(0); }
  100% {
    transform: translateY(0); } }

@keyframes move-up {
  0% {
    transform: translateY(0); }
  10% {
    transform: translateY(0); }
  20% {
    transform: translateY(-5px); }
  30% {
    transform: translateY(0); }
  100% {
    transform: translateY(0); } }

.accordion__title--animated:hover .accordion__arrow {
  animation-name: move-down;
  animation-duration: 1.5s; }

.accordion__title--animated[aria-expanded='true']:hover .accordion__arrow {
  animation-name: move-up;
  animation-duration: 1.5s; }

